.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative;
  
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
}

.i-intro {
  font-size: 48px;
  font-weight: 300;
}

.i-name {
  font-size: 75px;
}

.i-title-sub {
  font-size: 39px;
  font-weight: 400;
  margin-top: 15px;
  margin-bottom: 25px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 7s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-100px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 39px;
  font-weight: bold;
  color: var(--green);
  display: flex;
  align-items: center;
}

.i-img {
  width: 95%;
  height: 100%;
  object-fit: contain;
  position: absolute;
}

.i-bg {
  clip-path: polygon(100% 0%, 100% 48%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: var(--green);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

.i-scroll-full {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}
.i-scroll-dot {
  animation: scroll 1s ease-in-out infinite alternate;
}

@keyframes scroll {
  25% {
    transform: translateY(10px);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(10px);
  }
}
