.f {
  height: 100vh;
  display: flex;
  align-items: center;
}

.f-card.bg {
  position: absolute;
  right: 200px;
  top: 50px;
  background-color: var(--dark-blue);
}

.f-card {
  width: 43%;
  height: 79vh;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  border: 1px solid var(--dark-blue);
  background-color: var(--white);
}

.f-img {
  height: 100%;
  object-fit: cover;
}

.f-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.f-left {
  flex: 1;
  clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 0 49%, 0% 0%);
  background-color: var(--green);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 40px;
  overflow: hidden;
}

.f-preface {
  text-transform: uppercase;
  font-weight: 800;
  font-size: 150px;
  color: var(--bright-green);
  padding-top: 30px;
  padding-bottom: 20px;
}

.f-title {
  text-transform: capitalize;
  font-weight: 300;
  font-size: 55px;
  color: var(--white);
}

.f-subtitle {
  font-size: 25px;
  text-transform: capitalize;
}

.f-desc {
  padding-top: 5px;
  font-size: 18px;
  /* text-transform: capitalize; */
}

.f-desc strong {
  color: #e6e6e6;
  font-weight: 500;
}

.f-logo-container {
  margin-top: 20px;
  height: 30px;
  border: 2px solid var(--green);
  transition: border-radius 0.45s ease, border-color 0.45s ease,
    box-shadow 0.45s ease;
}

.f-logo {
  height: 100%;
  width: 100%;
}

.f-logo-container:hover {
  border-color: black;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: 10px 10px 15px -1px rgba(0, 0, 0, 0.22);
  -webkit-box-shadow: 10px 10px 15px -1px rgba(0, 0, 0, 0.22);
  -moz-box-shadow: 10px 10px 15px -1px rgba(0, 0, 0, 0.22);
}
